<script>
import { FetchApiWithCustomHeader } from "@/api/APIFetch";
import { Method, AppointmentAPI, ContentType } from "@/api/ListAPI";
import { LocalStorageKey } from "@/constant/LocalStorageKey";
import province from '@/constant/province';

export default {
    name: "EvaluateRequestModal",
    methods: {
        onClickCloseRequestModal() {
            this.$emit('oncloseRequest')
        },

        updateIsLoading(newLoadingState) {
            this.$store.commit("setIsLoading", newLoadingState);
        },

        validateData() {
            var isCheck = true;
            if (this.name == ''||this.phone==''||this.email==''||this.role==''||this.want==''||this.location=='') {
                isCheck = false;
            }
            if (isCheck) {
                this.check_data_message = '';
                let data = {
                    "fullName": this.name,
                    "phoneNumber": this.phone,
                    "email": this.email,
                    "guestRole": this.role,
                    "guestDesire": this.want,
                    "guestCity": this.location
                }
                this.saveEvaluateRequestFrom(data)
            } else {
                this.check_data_message = 'Vui lng in y  thng tin trc khi gi.'
            }
        },

        saveEvaluateRequestFrom(data) {
            let tokenType = this.$cookies.get(LocalStorageKey.TOKEN_TYPE_KEY);
            let token = this.$cookies.get(LocalStorageKey.TOKEN_KEY);
            this.updateIsLoading(true);
            FetchApiWithCustomHeader(
                AppointmentAPI.CreateAppointment,
                Method.POST,
                {
                    Content_Type: ContentType.JSON,
                    Authorization: `${tokenType} ${token}`,
                },
                {
                    fullName: data.fullName,
                    phoneNumber: data.phoneNumber,
                    email: data.email,
                    guestRole: data.guestRole,
                    guestDesire: data.guestDesire,
                    guestCity: data.guestCity
                },
                null,
                (response) => {
                    if (response.result === "OK") {
                        console.log(response)
                        this.$emit("closeModal",true);
                    } else {
                        // do something
                    }
                    this.updateIsLoading(false);
                }
            );
        }
    },
