<template>
  <div class="col-12" v-show="!this.$store.state.isLoading">
    <div class="d-inline-flex">
      <div>
        <img class="rounded-circle ava" :src="this.studentInfor.avatar ||  require('@/assets/avatar-demo.jpg')"  />
        <p class="text-center">
          Bit danh: <span style="font-weight: bold">{{ this.studentInfor.nickName }}</span>
        </p>
      </div>
      <div class="personal_info">
        <h5 style="font-weight: bold">THNG TIN C NHN:</h5>
        <p>M hc sinh: VSE-0{{ this.studentInfor.studentId || "Khng c" }}</p>
        <p>H v tn: {{ this.studentInfor.fullName || "Khng c"}}</p>
        <p>Ngy sinh: {{ this.studentInfor.birthDate || "Khng c"}}  </p>
        <p>Tui:</p>
        <p>a ch: {{ this.studentInfor.address || "Khng c" }}</p>
        <p>Ngy bt u: </p>
      </div>
    </div>
    <h5 style="font-weight: bold; margin: 10px">THNG TIN PH HUYNH:</h5>
    <div class="col-12 d-inline-flex parent_info">
      <div class="col-6">
        <p>H v tn b: {{ this.studentInfor.dadName || "Khng c" }}</p>
        <p>Ngy sinh: {{ this.studentInfor.dadBirthDate || "Khng c" }} </p>
        <p>Cng vic: {{ this.studentInfor.dadJob || "Khng c" }}</p>
        <p>S in thoi b: {{ this.studentInfor.dadPhone || "Khng c" }}</p>
        <p>Email: {{ this.studentInfor.dadEmail || "Khng c" }}</p>
      </div>
      <div class="col-5">
        <p>H v tn m: {{this.studentInfor.momName || "Khng c"}}</p>
        <p>Ngy sinh: {{ this.studentInfor.momBirthDate || "Khng c" }}</p>
        <p>Cng vic: {{ this.studentInfor.momJob || "Khng c" }}</p>
        <p>S in thoi m: {{this.studentInfor.momPhone || "Khng c" }}</p>
        <p>Email: {{this.studentInfor.momEmail || "Khng c" }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import { FetchApiWithCustomHeader } from "@/api/APIFetch";
import { ContentType, Method, StudentAPI } from "@/api/ListAPI";
import { LocalStorageKey } from "@/constant/LocalStorageKey";

export default {
  name: "StudentInfo",
  props: {},
  created() {
    this.getStudentInfor()
  },
  methods: {
    changeLoadingState(newLoadingState) {
      this.$store.commit("setIsLoading", newLoadingState);
    },
    updateStudentInfor(newStudentInfor) {
        this.studentInfor = {...newStudentInfor}
    },
    getStudentInfor() {
      let tokenType = this.$cookies.get(LocalStorageKey.TOKEN_TYPE_KEY);
      let token = this.$cookies.get(LocalStorageKey.TOKEN_KEY);
      this.changeLoadingState(true);
      FetchApiWithCustomHeader(
        StudentAPI.GetStudentInfor + `/${this.$route.params.id}`,
        Method.GET,
        {
          Content_Type: ContentType.JSON,
          Authorization: `${tokenType} ${token}`,
        },
        null,
        null,
        (response) => {
          if (response.result === "OK") {
            this.updateStudentInfor(response.data)
          } else {
            // do something
          }
          this.changeLoadingState(false);
        }
      );
    },
  },
  data() {
    return {
      studentInfor: {},
    };
  },
};
</script>

<style scoped>
img.ava {
  width: 200px;
  height: 200px;
  margin: 3rem 3rem 2rem 4rem;
}

div.personal_info {
  padding-top: 2rem;
}

div.personal_info p {
  padding-left: 2rem;
}

div.parent_info p {
  padding-left: 2rem;
}
</style>
